.skill-set-div {
  display: flex;
  justify-content: space-between; 
  width: 70%;
  margin: 0 15%;
}

.skillset-container {
  margin-bottom: 100px;
}

.skillset-row {
  margin: 40px 40px 60px;
}

.skillset-avatar-img {
  /* border: 2px solid #2C394B; */
  border-radius: 50%;  
  height: 220px;
  width: 220px;
  vertical-align: middle; 
  margin: 30px 15px 20px; 
}

.skill-set-col {
  text-align: center;
  width: 25%;   
}

.about-me-heading {
  font-family: 'Kanit', sans-serif;
  font-size: 45px;
  margin: 60px 20px 30px;
}

.about-me-text {
  font-family: 'Gowun Dodum', sans-serif;
  font-size: 25px;
}

.skill-set-desc {
  font-family: 'Comfortaa', cursive;
  font-size: 25px;
}